import React, { useEffect, useReducer } from 'react';
import Layout from "../components/layout"
import TableOfContents from '../components/tableOfContents'; // 20201002 TOC 추가
import { Box, Text } from 'rebass/styled-components'; // 20201002 TOC 추가
import { graphql } from "gatsby";

import { Helmet } from "react-helmet"; // 20201012 mathjax 관련

export default function BlogPost({data, location}) {
  const post = data.markdownRemark;

 // 20201002 TOC 추가
  const tocItems = data.markdownRemark.tableOfContents;
  const [currentHeaderUrl, setCurrentHeaderUrl] = useReducer(undefined);


  useEffect(() => {
    const handleScroll = () => {
      let aboveHeaderUrl;
      const currentOffsetY = window.pageYOffset;
      const headerElements = document.querySelectorAll('.anchor-header');
      for (const elem of headerElements) {
        const { top } = elem.getBoundingClientRect();
        const elemTop = top + currentOffsetY;
        const isLast = elem === headerElements[headerElements.length - 1];
        if (currentOffsetY < elemTop - HEADER_OFFSET_Y) {
          aboveHeaderUrl &&
            setCurrentHeaderUrl(aboveHeaderUrl.split(location.origin)[1]);
          !aboveHeaderUrl && setCurrentHeaderUrl(undefined);
          break;
        } else {
          isLast && setCurrentHeaderUrl(elem.href.split(location.origin)[1]);
          !isLast && (aboveHeaderUrl = elem.href);
        }
      }
    };
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [location.origin]);

  const isTOCVisible = tocItems?.length > 0;
  const { title, date } = post.frontmatter;

  return (

    <Layout location={location} data={data}>
    <Box sx={{ position: 'relative' }}>
      {isTOCVisible && (
        <Box
          sx={{
            position: 'absolute',
            top: 0,
            height: '100%',
            right: 'calc((100vw - 720px) / 2 * (-1))',
          }}
        >
          <TableOfContents
            items={tocItems}
            currentHeaderUrl={currentHeaderUrl}
          />
        </Box>
      )}
      <Box as="header">
        <Text fontSize="24px" lineHeight="1.1">
          {title}
        </Text>
        <Text mb="70px" mt="7px" color="#bbb">
          {date}
        </Text>
      </Box>
      <Box
        dangerouslySetInnerHTML={{ __html: post.html }}
        css={{ lineHeight: '30px', position: 'static' }}
      />

      <Helmet>
        <script>{"MathJax = {tex: {inlineMath: [['$', '$']]}}"}</script>
        <script
            id="MathJax-script"
            async
            src="https://cdn.jsdelivr.net/npm/mathjax@3/es5/tex-mml-chtml.js"
        ></script>
      </Helmet>

    </Box>

    </Layout>

  )
}
//

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      tableOfContents
      frontmatter {
        title
      }
    }
  }
`
const HEADER_OFFSET_Y = 100;
